<template>
    <div class="m-publish-box m-publish-box-emotion" v-loading="loading">
        <!--  -->
        <publish-header name=""></publish-header>

        <el-form label-position="left" label-width="80px">
            <!--  -->
            <div class="m-publish-emotion m-publish-emotion-edit" v-if="id">
                <el-form-item label="">
                    <div class="u-pic" v-if="data && data.url">
                        <img class="u-img" :src="data.url | showThumbnail" />
                        <i class="u-mask"></i>
                        <i class="u-preview" @click="previewHandle(data)">
                            <i class="el-icon-zoom-in"></i>
                        </i>
                    </div>
                    <div class="m-emotion-upload" v-show="!data.url">
                        <div v-if="data && data.url" class="u-emotion">
                            <img :src="data.url" />
                            <i class="u-emotion-mask"></i>
                            <i
                                class="u-emotion-delete el-icon-delete"
                                title=""
                                @click="handleRemove"
                            ></i>
                        </div>
                        <div
                            v-else
                            class="u-upload el-upload el-upload--picture-card"
                            @click="select"
                        >
                            <i class="el-icon-plus"></i>
                        </div>
                        <input
                            class="u-upload-input"
                            type="file"
                            @change="upload"
                            ref="uploadInput"
                        />
                    </div>
                    <el-button
                        v-if="data && data.url"
                        class="u-remove-btn"
                        icon="el-icon-delete"
                        @click="removePic"
                        size="mini"
                    ></el-button>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="data.desc" :maxlength="120" show-word-limit placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="data.type" placeholder="">
                        <el-option v-for="(item,i) in schoolmap" :key="i" :value="i" :label="item">
                            <div style="display: flex;align-items: center;">
                                <img class="u-icon" style="margin-right: 20px" width="24" height="24" :src="i | showSchoolIcon" :alt="item" />
                                {{item}}
                            </div>
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-switch v-model.number="data.original" :active-value="1" :inactive-value="0"></el-switch>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="data.author" placeholder=""></el-input>
                </el-form-item>
                <!--  -->
                <div class="m-publish-buttons">
                    <el-button
                        type="primary"
                        @click="update"
                        :disabled="processing"
                    > &nbsp;&nbsp; </el-button>
                </div>
            </div>

            <!--  -->
            <div class="m-publish-emotion m-publish-emotion-create" v-else>
                <!--  -->
                <div class="m-publish-emotion-bulk">
                    <h1 class="u-title"></h1>
                    <UploadEmotion
                        @insert="updateFileList"
                        text=""
                        :onlyImage="true"
                        desc="105M"
                        :accept="supportTypes"
                    />
                    <div class="u-tip">
                        <i class="el-icon-info"></i> gif/png/jpg/bmp/webp
                    </div>
                </div>

                <!--  -->
                <div class="m-publish-emotion-list" v-if="list && list.length">
                    <div class="u-list">
                        <div class="u-item" v-for="(item, i) in list" :key="i">
                            <div class="u-div">
                                <i class="u-order">{{ i + 1 }}</i>
                                <div class="u-op">
                                    <el-button
                                        class="u-delete"
                                        icon="el-icon-delete"
                                        @click="deleteHandle(i)"
                                        type="info"
                                        size="mini"
                                    ></el-button>
                                </div>
                            </div>
                            <div class="u-pic">
                                <img class="u-img" :src="item.url | showThumbnail" />
                                <i class="u-mask"></i>
                                <i class="u-preview" @click="previewHandle(item)">
                                    <i class="el-icon-zoom-in"></i>
                                </i>
                            </div>
                            <div class="u-desc">
                                <el-input v-model="item.desc" :maxlength="120" show-word-limit placeholder="">
                                    <span slot="prepend"></span>
                                </el-input>
                            </div>
                            <div class="u-extend">
                                <el-switch
                                    v-model.number="item.original"
                                    inactive-text=""
                                    :active-value="1"
                                    :inactive-value="0"
                                ></el-switch>
                                <el-input
                                    class="u-author"
                                    v-model="item.author"
                                    placeholder=""
                                    v-if="!item.original"
                                    size="mini"
                                >
                                    <span slot="prepend"></span>
                                </el-input>
                                <el-select v-model="item.type" size="mini" style="margin-left: 10px;" placeholder="">
                                    <el-option v-for="(school,i) in schoolmap" :key="i" :value="i" :label="school">
                                        <div style="display: flex;align-items: center;">
                                            <img class="u-icon" style="margin-right: 20px" width="24" height="24" :src="i | showSchoolIcon" :alt="item" />
                                            {{school}}
                                        </div>
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </div>
                    <!--  -->
                    <div class="m-publish-buttons">
                        <el-button
                            type="primary"
                            @click="publish"
                            :disabled="processing"
                        > &nbsp;&nbsp; </el-button>
                    </div>
                </div>
            </div>
        </el-form>
        <!--  -->
        <el-dialog class="m-publish-emotion-preview" :visible.sync="dialogVisible" title="">
            <img :src="dialogImageUrl" />
        </el-dialog>
    </div>
</template>

<script>
// 
import { getLink, getThumbnail } from "@jx3box/jx3box-common/js/utils";
import schoolmap from "@jx3box/jx3box-data/data/xf/schoolid.json";
import { __imgPath, __cdn } from "@jx3box/jx3box-common/data/jx3box.json";

// 
import publish_header from "@/components/publish_header.vue";
import UploadImages from "@jx3box/jx3box-editor/src/Upload.vue";
import UploadEmotion from "@/components/upload_emotion.vue";

// 
import {
    postEmotions,
    getEmotion,
    updateEmotion,
    uploadEmotion,
} from "@/service/pvx.js";

export default {
    name: "emotion",
    components: {
        // UploadImages,
        "publish-header": publish_header,
        UploadEmotion,
    },
    data: function () {
        return {
            // 
            loading: false,
            // 
            processing: false,

            schoolmap,

            // 
            supportTypes:
                "image/png, image/jpeg, image/gif, image/bmp, image/webp",

            // 
            list: [],
            data: {},

            // 
            dialogImageUrl: "",
            dialogVisible: false,
        };
    },
    computed: {
        id: function () {
            return ~~this.$route.params.id;
        },
        fileInput: function () {
            return this.$refs.uploadInput;
        },
    },
    methods: {
        // 
        init: function () {
            this.loading = true;
            getEmotion(this.id)
                .then((res) => {
                    this.data = res?.data?.data;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        // 
        updateFileList: function (data) {
            let upload_list = data.list;
            let img_list = [];
            upload_list.forEach((item) => {
                if (item.is_img) {
                    img_list.push({
                        desc: item.name,
                        url: item.url,
                        original: 0,
                        author: "",
                        type: ''
                    });
                }
            });
            this.list = this.list.concat(img_list);
        },
        // 
        previewHandle: function (item) {
            this.dialogImageUrl = item.url;
            this.dialogVisible = true;
        },
        // 
        deleteHandle: function (i) {
            this.list.splice(i, 1);
        },
        // 
        publish: function () {
            this.processing = true;
            postEmotions(this.list)
                .then((res) => {
                    this.$message({
                        message: ",",
                        type: "success",
                    });
                    this.list = []
                })
                .finally(() => {
                    this.processing = false;
                });
        },
        // 
        update: function () {
            if (!this.data?.url) {
                this.$message({
                    message: "",
                    type: "warning",
                });
                return;
            }
            this.processing = true;
            updateEmotion(this.id, this.data)
                .then((res) => {
                    this.$message({
                        message: ",",
                        type: "success",
                    });
                    // 
                    setTimeout(() => {
                        location.href = getLink("emotion", this.id);
                    }, 500);
                })
                .finally(() => {
                    this.processing = false;
                });
        },
        //  
        removePic: function () {
            this.data.url = "";
        },
        select: function () {
            this.fileInput.dispatchEvent(new MouseEvent("click"));
        },
        upload: function () {
            let formdata = new FormData();
            formdata.append("file", this.fileInput.files[0]);
            uploadEmotion(formdata).then((res) => {
                this.data.url = __cdn + res.data.data[0];
                this.$message({
                    message: "",
                    type: "success",
                });

                this.fileInput.files = [];
            });
        },
        handleRemove() {
            this.data.url = "";
        },
    },
    watch: {
        "$route.params.id": {
            immediate: true,
            handler: function (val) {
                val && this.init();
            },
        },
    },
    filters: {
        showThumbnail: function (val) {
            return getThumbnail(val, 146);
        },
        showSchoolIcon: function (val) {
            return __imgPath + "image/school/" + val + ".png";
        },
    },
};
</script>

<style lang="less">
@import "../assets/css/emotion.less";
</style>
